.slider-container {
    position: relative;
}

.slider {
    margin-top: 5px;

    &__item {
        position: relative;
    }

    &__item img {
        display: block;
        width: 100%;
    }

    &__darkness {
        position: absolute;
        height: 100%;
        left: 0;
        top: 0;
        background: linear-gradient(90deg, rgba(26, 16, 1, 0.648) 67.33%, rgba(196, 196, 196, 0) 101.32%);
        width: 70%;
    }

    &__info {
        position: absolute;
        top: 94px;
        bottom: 0;
        left: 30px;
        margin: auto 0;
        width: 70%;
    }

    &__title {
        font-family: 'Forum', sans-serif;
        color: #fff;
        font-size: 56px;
        margin-bottom: 30px;
    }

    &__subtitle {
        font-size: 28px;
        color: #fff;
        margin-bottom: 30px;
    }

    &__btn {
        background: $orange;
        color: #fff;
        border-radius: 4px;
        font-size: 18px;
        text-decoration: none;
        padding: 30px 40px;
        display: inline-block;
        cursor: pointer;
        transition: all .3s;
        text-align: center;

        &:hover {
            background: darken($orange, 5%);
        }
    }

    &__btn--mobile {
        display: none;
    }
}


.main-nav {
    position: absolute;
    left: 30px;
    bottom: 60px;
    width: 150px;
}

.main-nav-wrp {
    position: relative;
    width: 100%;
}


.pr-prev,
.pr-next {
    border: none;
    background: transparent;
    font-size: 40px;
    font-weight: 700;
    color: #fff;
    position: absolute;

    &:hover {
        color: $orange;
    }

}

.pr-prev {
    left: 0;
}

.pr-next {
    right: 0;
}

.my-dots {
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
    top: 10px;
    z-index: 50;

}

.slick-dots {
    display: flex !important;
    flex-direction: row;
    justify-content: center;
    align-items: center;

}

.slick-dots > li {
    width: 13px;
    height: 13px;
    margin: 0 5px;
}

.slick-dots > li > button {

    background: $orange;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    border: none;
    font-size: 0;

    &:hover {
        background: #fff;
    }

}

.slick-dots > li.slick-active > button {
    background: #fff;

    &:hover {
        background: $orange;
    }

}

@media only screen and (max-width : 1200px) {
    .slider {

        &__info {
            top: 50px;
            left: 20px;
            margin: auto 0;
            width: 70%;
        }

        &__title {
            font-size: 32px;
            margin-bottom: 30px;
        }

        &__subtitle {
            font-size: 20px;
            margin-bottom: 30px;
        }

        &__btn {
            font-size: 18px;
            padding: 30px 40px;
        }
    }

}

@media only screen and (max-width : 844px) {
    .slider {


        &__btn {
            display: none;
        }

        &__btn--mobile {
            display: block;
            width: 100%;
            border-radius: 0px;
        }
    }
}

@media only screen and (max-width : 640px) {
    .slider {

        &__info {
            top: 30px;
            left: 20px;

        }

        &__title {
            font-size: 24px;
        }

        &__subtitle {
            font-size: 16px;
        }

    }



    .main-nav {
        left: 15px;
        bottom: -5px;
        width: 120px;
    }

    .pr-prev,
    .pr-next {
        color: $orange;
        font-size: 24px;
    }

    .my-dots {

        top: 0px;
    }


    .slick-dots > li > button {

        background: $orange;

        &:hover {
            background: $orange
        }

    }

    .slick-dots > li.slick-active > button {
        background: $orange;
        opacity: .7;



    }



}

@media only screen and (max-width : 576px) {
    .slider {

        &__info {
            top: 30px;
            left: 20px;
            width: 80%;

        }

        &__title {
            font-size: 18px;
            margin-bottom: 20px;

        }

        &__subtitle {
            font-size: 14px;
        }

    }
}


@media only screen and (max-width : 344px) {
    .slider {

        &__info {
            top: 15px;
            left: 20px;
            width: 90%;

        }

        &__title {
            font-size: 18px;
            margin-bottom: 20px;

        }

        &__subtitle {
            font-size: 14px;
        }

    }

}
