.tizers-wrp {
    padding: 90px 0 0 0;
}

.block-title--centered {
    text-align: center;
}

.tizers {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: 70px 0;
}

.tizer {
    flex-basis: 30%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 40px;

    &__img {
        width: 80px;
        height: 80px;
        margin-bottom: 20px;
    }

    &__title {
        color: $fontd;
        margin-bottom: 20px;
        line-height: 1.3;
        text-align: center;
        font-weight: 700;
        font-size: 18px;
    }

    &__descr {
        color: $fontl;
        line-height: 1.3;
        text-align: center;
    }
}

@media only screen and (max-width : 903px) {
    .tizer {
        flex-basis: 45%;
    }
}

@media only screen and (max-width : 576px) {
    .tizer {
        flex-basis: 100%;

        &__img {
            width: 50px;
            height: 50px;

        }

        &__title {

            font-size: 16px;
        }

        &__descr {
            font-size: 16px;

        }

    }
}
