// 

.page {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}




.footer {
    margin-top: auto;
    padding: 40px 0;
    background: #F5F5F5;

    hr {
        background-color: lightgray;
        margin-bottom: 20px;
    }

    &__logos {
        text-align: center;

        img {
            max-width: 150px;
            margin-bottom: 40px;
        }

        p {
            color: $fontd;
            line-height: 1.3;
            margin-bottom: 20px;
        }

    }


    &__title {

        color: $fontd;
        font-size: 20px;
        margin-bottom: 30px;
        font-weight: 700;
        text-transform: uppercase;
    }

    &__phones {
        margin-bottom: 30px;
    }

    &__phone {
        display: block;
        color: $fontd;
        font-size: 25px;
        margin-bottom: 10px;
        font-weight: 700;

    }

    &__ws {
        margin-bottom: 20px;

        a {
            font-size: 16px;
            color: $fontd;
            opacity: .5;

            &:hover {
                opacity: 1;
            }
        }
    }

    &__contact {
        display: flex;
        margin-bottom: 20px;


        a {
            font-size: 16px;
            color: $fontd;
            opacity: .5;
            margin-bottom: 15px;
            display: block;
            line-height: 1.4;

            &:hover {
                opacity: 1;
            }

        }

        .soc-link {
            display: flex;
        }
    }

    &__ic {
        width: 17px;
        height: 17px;
        fill: $orange;
        margin-right: 10px;
        margin-top: 5px;
    }

    &__menu {
        margin-bottom: 30px;

        a {
            font-size: 16px;
            color: $fontd;
            opacity: .5;
            margin-bottom: 15px;
            display: block;
            line-height: 1.4;

            &:hover {
                opacity: 1;
            }

        }
    }

    &__info {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: flex-start;
    }

    &__privacy {
        flex-basis: 50%;
        font-size: 12px;
        color: $fontl;
        opacity: .5;
        line-height: 1.4;
    }

    &__delosait {
        flex-basis: 20%;
        text-align: end;

        a {
            font-size: 12px;
            opacity: .5;
            color: $fontl;

            &:hover {
                opacity: 1;
            }
        }
    }
}

.payment {
    display: flex;

    &__item {
        margin-right: 11px;
    }
}

@media only screen and (max-width : 560px) {
    .footer {
        &__privacy {
            flex-basis: 100%;
            margin-bottom: 20px;
        }

        &__delosait {
            flex-basis: 100%;
            margin-bottom: 20px;
        }

        &__title {
            margin-bottom: 20px;
            font-size: 20px;
        }

        &__menu {

            a {
                font-size: 14px;
                line-height: 1.3;
            }
        }

        &__phone {
            font-size: 20px;
        }

        &__contact {
            a {
                font-size: 14px;
                margin-bottom: 0px;
            }
        }

        &__ic {
            margin-top: 0;
        }



    }

}
