#stages {
    padding: 40px 0;
}

.stages {
    display: flex;
    justify-content: space-between;
    padding: 20px 0 40px 0;

}

.stage {
    background: #F2EDEA;
    padding: 90px 20px 20px 20px;
    position: relative;
    height: 416px;
    margin-right: 30px;
    margin-bottom: 20px;

    &:last-child {
        margin-right: 0;
    }

    &__title {
        font-size: 18px;
        font-weight: 700;
        margin-bottom: 80px;
        position: relative;
        z-index: 10;

        &::after {
            content: "";
            width: 57px;
            height: 6px;
            display: block;
            position: absolute;
            left: 0;
            bottom: -35px;
            background: $orange;

        }
    }

    &__info {
        color: $fontl;
        line-height: 1.3;
        font-size: 18px;
        z-index: 10;
        position: relative;
    }

    &__img {
        position: absolute;
        right: 0;
        bottom: 0;
        z-index: 5;
    }
}

.stage1,
.stage4 {
    flex-basis: 25%;
}

.stage2 {
    flex-basis: 20%;
}

.stage3 {
    flex-basis: 30%;
    background: linear-gradient(77deg, #583E2D 0%, #010101 95.43%);

    .stage__title {
        color: #fff;
    }

    .stage__info {
        color: #fff;
    }

}

.stage4 {
    .stage__info {
        padding-right: 50px;
    }
}

@media only screen and (max-width : 1399px) {
    .stage4 {
        .stage__info {
            padding-right: 80px;
        }
    }

}

@media only screen and (max-width : 1200px) {
    .stages {
        flex-wrap: wrap
    }

    .stage {
        margin-right: 0;
    }

    .stage1,
    .stage4 {
        flex-basis: 45%;
    }

    .stage2 {
        flex-basis: 45%;
    }

    .stage3 {
        flex-basis: 45%;

    }
}

@media only screen and (max-width : 576px) {
    .stage {
        padding: 30px 20px 20px 20px;
        height: 316px;
    }

    .stage1,
    .stage4 {
        flex-basis: 100%;
    }

    .stage2 {
        flex-basis: 100%;
    }

    .stage3 {
        flex-basis: 100%;

    }

    .stage4 {
        .stage__info {
            padding-right: 70px;
        }
    }

}
