.mobile_tabs {
    display: none;
}

@media only screen and (max-width : 922px) {

    .mobile_tabs {
        display: block;
    }

    .accordion-menu {

        max-width: 100%;
        margin: 0px 0px 20px;
        border-radius: 4px;
        box-shadow: 0px 5px 19px #3c3c3c29;
        border-radius: 4px;
    }

    .accordion-menu li.open .dropdownlink {
        color: $orange;

        .fa-chevron-down {
            transform: rotate(180deg);
        }
    }

    .accordion-menu li:last-child .dropdownlink {
        border-bottom: 0;
    }

    .dropdownlink {
        cursor: pointer;
        display: block;
        padding: 30px 15px 30px 15px;
        font-size: 18px;
        border-bottom: 1px solid #e8e8e8;
        color: $fontd;
        position: relative;
        transition: all 0.4s ease-out;
        text-transform: uppercase;
        font-weight: 700;

        span {
            color: #fff;
        }

        i {
            position: absolute;
            top: 30px;
            left: 16px;
        }

        .fa-chevron-down {
            right: 12px;
            left: auto;
            font-size: 15px;
            color: $fontd;
        }
    }

    .question_info {
        display: none;
        background: #f3f3f3;
        padding: 15px;
        line-height: 1.5;
        font-size: 17px;



    }
}

.submenuItems a {
    display: block;
    color: #727272;
    padding: 15px 12px 15px 45px;
    transition: all 0.3s ease-out;
    text-decoration: none;

    &:hover {
        background-color: #fff;
        color: #727272;
    }
}



@media only screen and (max-width : 641px) {
    .mobile_tabs {
        margin-top: 50px;

    }
}

@media(max-width:540px) {
    .dropdownlink {
        font-size: 15px;
    }

    .question_info {
        font-size: 15px;
    }

    .bg2 {
        padding: 50px 0 120px 0;

    }

    .bg2 h4 {
        font-size: 28px;

    }

    .slider2_info h6 {
        font-size: 24px;

    }

    .btn-red {
        padding: 17px 45px;

    }
}
