.collections {
    padding: 70px 0 0 0;
}

.block-title {
    font-family: 'Forum', sans-serif;
    font-size: 56px;
    margin-bottom: 30px;
}

.tab_nav {
    width: 100%;
}

.collect {
    display: flex;
    flex-direction: column;

    &__info p {
        font-size: 18px;
        color: $fontl;
        line-height: 1.4;
        margin-bottom: 20px;
    }

    &__arrows,
    &__arrows2,
    &__arrows3,
    &__arrows4,
    &__arrows5,
    .tab-arrows {
        margin-bottom: 20px;
        position: relative;
        z-index: 600;

        .pr-prev1 {
            width: 103px;
            height: 46px;
            background: #F2EDEA;
            font-size: 20px;
            color: #66727F;
            border: none;
            border-radius: 4px;
            margin-right: 10px;
        }

        .pr-next1 {
            width: 103px;
            height: 46px;
            background: #F2EDEA;
            font-size: 20px;
            color: #66727F;
            border: none;
            border-radius: 4px;
        }
    }

}



.c_item {
    height: 377px;
    margin-right: 20px;
    cursor: pointer;


    a {
        width: 100%;
        height: 100%;
        display: block;


        img {
            height: 520px;
            object-fit: cover;
        }
    }
}

.tab-slider {
    margin-right: -50%;
    position: relative;
    z-index: 200;

}

.menu-items {
    color: $fontl;
    background: #fff;
    padding: 18px 15px;
    border-bottom: 1px solid #ececec !important;
    text-align: left;

    &:hover {
        color: $orange;
    }
}

.nav-pills .nav-link.active,
.nav-pills .show > .nav-link {
    color: $fontd;
    font-weight: 700;
    background-color: #fff;
}

.production {

    &__img {
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 20px 0;
    }

    &__img img {
        display: block;
    }
}

.btn-orange {
    background: $orange;
    color: #fff;
    border-radius: 4px;
    font-size: 18px;
    text-decoration: none;
    padding: 30px 40px;
    display: inline-block;
    cursor: pointer;
    transition: all .3s;
    text-align: center;

    &:hover {
        background: darken($orange, 5%);
        color: #fff;
    }

}

.btn-catalog {
    width: 100%;
}


/* bootstrap hack: fix content width inside hidden tabs */
.tab-content > .tab-pane,
.pill-content > .pill-pane,
    {
    display: block;
    height: 0;
    /*    overflow: hidden;*/

}

.tab-content > .active,
.pill-content > .active {
    height: auto;

}

/* end bootstrap hack: fix content width inside hidden tabs */


@media only screen and (max-width : 1200px) {

    .tab-slider {
        margin-right: 0;
    }

    .btn-catalog {
        width: 100%;
        padding: 30px 15px;
    }

    .block-title {
        font-size: 32px;
    }

}

@media only screen and (max-width : 922px) {
    .collections {
        display: none;
    }
}

@media only screen and (max-width : 922px) {

    .c_item {
        height: auto;
        margin-right: 0px;

        a {
            img {
                height: auto;

            }
        }
    }
}

@media only screen and (max-width : 576px) {
    .block-title {
        font-size: 22px;

    }

    .collect__info p {
        font-size: 16px;
    }
}
