        .menu_mobile {
            position: fixed;
            display: block;
            width: 20%;
            height: 100%;
            left: -100%;
            top: 0;
            z-index: 1;
            background: #fbfbfb;
            transition: left .3s .3s;
            z-index: 920;
        }

        .menu_mobile.active {
            left: 0;
            transition: left .3s;
        }

        .menu_mobile__wrp {
            position: relative;
        }

        .close-menu {
            transform: scale(0);
            transition: transform 1s;
            position: absolute;
            right: 10px;
            top: 10px;
            position: absolute;
        }



        .left_menu {
            text-align: left;
            padding-top: 40px;
            padding-left: 40px;

            li {
                margin-bottom: 20px;

                a {
                    font-size: 18px;
                    color: $fontd;
                }
            }
        }

        .mobile_block {
            display: none;
        }

        @media only screen and (max-width: 964px) {
            .mobile_block {
                display: flex;
                justify-content: center;
                flex-direction: column;
                text-align: center;
            }
        }

        @media only screen and (max-width: 660px) {
            .menu_mobile {

                width: 100%;
                text-align: center;



            }
        }

        #nav-icon {
            width: 40px;
            height: 30px;
            position: relative;
            transform: rotate(0deg);
            transition: .3s ease-in-out;
            cursor: pointer;
            margin-right: 25px;
            margin-top: 10px;
        }


        #nav-icon span {
            display: block;
            position: absolute;
            height: 3px;
            width: 100%;
            background: $orange;
            border-radius: 9px;
            opacity: 1;
            left: 0;
        }


        #nav-icon span:nth-child(1) {
            top: 0px;
        }

        #nav-icon span:nth-child(2),
        #nav-icon span:nth-child(3) {
            top: 12px;
        }

        #nav-icon span:nth-child(4) {
            top: 24px;
        }



        /**/

        .close-menu {
            width: 25px;
            height: 25px;
            transform: rotate(0deg);
            cursor: pointer;
            margin-top: 20px;
            margin-left: 10px;
        }


        .close-menu span {
            display: block;
            position: absolute;
            height: 2px;
            width: 100%;
            background: $fontd;
            border-radius: 9px;
            opacity: 1;
            left: 0;
            transform: rotate(0deg);

        }


        .close-menu span:nth-child(1) {
            top: 12px;
            width: 0%;
            left: 50%;
        }

        .close-menu span:nth-child(2) {
            transform: rotate(45deg);
        }

        .close-menu span:nth-child(3) {
            transform: rotate(-45deg);
        }

        .close-menu span:nth-child(4) {
            top: 12px;
            width: 0%;
            left: 50%;
        }


        @media only screen and (max-width : 991px) {
            .h_menu {
                display: none;
            }

        }
